<template>
  <div>
    <el-dialog :title="info.title" :visible.sync="info.show">
  <el-form :model="form">
    <el-form-item label="规格名称" :label-width="formLabelWidth">
      <el-input v-model="form.specsname" autocomplete="off"></el-input>
    </el-form-item>
    <el-form-item v-for="(item,index) in attrArr" :key="index" label="规格属性" label-width="80px">
        <el-input v-model="item.value"></el-input>
        <el-button v-if="index == 0" @click="addAttr">新增规格</el-button>
        <el-button v-else @click="delAttr(index)">删除</el-button>
    </el-form-item>
    <el-form-item label="菜单状态" :label-width="formLabelWidth">
      <el-switch  v-model="form.status" active-color="blue" inactive-color="grey" :active-value="1"
    :inactive-value="2">
</el-switch>
    </el-form-item>
  </el-form>
  <div slot="footer" class="dialog-footer">
    <el-button @click="cancel">取 消</el-button>
    <el-button type="primary" @click="add" v-if="info.isAdd==true">添加</el-button>
    <el-button type="primary" @click="update" v-else>修改</el-button>
  </div>
</el-dialog>

  </div>
</template>

<script>
import {requestSpecAdd,requestSpecOne,requestSpecUpdate} from '../../../util/request';
import {successAlert,warningAlert} from '../../../util/alert';
import {mapGetters, mapActions} from 'vuex';
export default {
  props:["info"],
  data() {
      return {
        attrArr:[
          {value:'',}
        ],
        form: {
          specsname: '',
          attrs:'',
          status:1,
        },
        formLabelWidth: '120px',
      };
    },
    methods:{
      addAttr(){
        this.attrArr.push({value:''});
      },
      delAttr(index){
        this.attrArr.splice(index,1);
      },
      // 重置内容
      empty(){
        this.form = {
          specsname: '',
          attrs:'',
          status:1,
        };

      },
      cancel(){
        this.info.show = false
        if(this.info.isAdd){
          this.empty();
        }
        this.attrArr = [
          {value:'',}
        ];
      },
      add(){
        if(this.attrArr.some((item)=>item.value == '')){
          warningAlert('规格属性不能为空');
          return;
        }
        // this.form.attrs = JSON.stringify(this.attrArr.map((item)=>item.value))
        this.form.attrs = this.attrArr.map((item)=>item.value).join(',');
        // console.log(this.form.attrs);
        // 发送axios请求
        requestSpecAdd(this.form).then((res)=>{
          if(res.data.code === 200){
            successAlert(res.data.msg);
            //重置form数据
            this.empty();
            //弹框消失
            this.cancel();
            this.attrArr=[{value:'',}];
            this.requestList();
            // 更新列表同时更新总记录数
            this.requestCount();
          }else{
            warningAlert(res.data.msg);
          }
        })

      },
      getDetail(id){
        // 根据ID获取某一调数据
        requestSpecOne({id:id}).then(res=>{
          // 将获取的该条数据展示在对话框中
          this.form = res.data.list[0];
          this.form.id = id;
          var arr = res.data.list[0].attrs.split(',');
          this.attrArr=[];
          for(let i in arr){
            this.attrArr.push({value:arr[i]})
          }
        })
      },
      update(){
        if(this.attrArr.some((item)=>item.value == '')){
          warningAlert('规格属性不能为空');
        }
        this.form.attrs = this.attrArr.map((item)=>item.value).join(',');
        // console.log(this.form.attrs);
        // 发送修改请求
        requestSpecUpdate(this.form).then(res=>{
          if(res.data.code == 200){
            successAlert('修改成功');
             this.empty()
            this.cancel()
            this.requestList();
          }else{
            warningAlert('修改失败');
          }
        })
      },
      ...mapActions({
        requestList:"spec/changeSpecActions",
        requestCount:"spec/changeCountActions"
      })
    },


}
</script>

<style>

</style>



