<template>
  <div>
     <el-table :data="list"
     row-key="id"
      border style="width: 100%">
      <el-table-column prop="id" label="规格编号" width="120"> </el-table-column>
      <el-table-column prop="specsname" label="规格名称" width="140">
      </el-table-column>
      <el-table-column prop="attrs" label="规格属性" width="300">
          <template slot-scope="scope">
           <el-tag v-for="item in scope.row.attrs" :key="item">{{item}}</el-tag>
        </template>
      </el-table-column>
      <el-table-column prop="status" label="状态" width="100">
        <template slot-scope="scope">
          <el-button type="primary" v-if="scope.row.status == 1">启用</el-button>
          <el-button type="danger" v-else>禁用</el-button>
        </template>
      </el-table-column>
      <el-table-column prop="name" label="操作" width="*">
        <template slot-scope="scope">
          <el-button type="primary" @click="edit(scope.row.id)">编辑</el-button>
          <el-button type="danger" @click="del(scope.row.id)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <el-pagination background layout="prev, pager, next" @current-change="cPage" :page-size="size" :total="count">
</el-pagination>
  </div>
</template>

<script>
import {requestSpecDel} from '../../../util/request';
import {mapGetters,mapActions} from 'vuex';
import { successAlert, warningAlert } from '../../../util/alert';
export default {
  computed:{
    ...mapGetters({
      list:"spec/list",
      count:"spec/count",
      size:"spec/size",
    })
  },
  methods:{
      ...mapActions({
        requestList:'spec/changeSpecActions',
        requestCount:'spec/changeCountActions',
        requestPage:'spec/changePageActions',
      }),
      edit(id){
        this.$emit('edit',id);
      },
      del(id){
        this.$confirm('确定要删除, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(()=>{
          requestSpecDel({id:id}).then(res=>{
            if(res.data.code == 200){
              successAlert('删除成功！！！---')
              this.requestCount();
              this.requestList();
            }else{
              warningAlert('删除失败');
            }
          })
        }).catch(error=>error)
      },
      cPage(page){
         this.requestPage(page);
         this.requestList();
      }
  },
  mounted(){
    // 自动发送请求
    this.requestList();
    this.requestCount();
  }
}
</script>

<style scoped>

</style>



